export class Draw {
  constructor(options) {
    // console.log(options)
    // canvas
    this.canvas = document.getElementById(options.element);
    this.ctx = this.canvas.getContext("2d");
    // 
    this.painting = false;
    this.lastPoint = { x: undefined, y: undefined };
    this.clear = false; //
    this.history = []; // 
    this.lineWidth = 3; // 
  }

  // 
  init() {
    this.canvas.width = document.body.clientWidth - 10;
    this.canvas.height = document.body.clientHeight - 10;
    this.canvas.onmousedown = e => {
      this.readyDraw(e);
    };
    this.canvas.onmousemove = e => {
      this.drawing(e);
    };
    this.canvas.onmouseup = () => {
      this.stopDraw();
    };
  }

  // 
  readyDraw(e) {
    let firstDot = this.ctx.getImageData(
      0,
      0,
      this.canvas.width,
      this.canvas.height
    ); //
    this.saveData(firstDot);
    this.painting = true;
    let x = e.offsetX;
    let y = e.offsetY;
    this.lastPoint = { x: x, y: y };
    this.ctx.save();
    this.drawCircle(x, y, 0);
  }

  // canvas
  drawing(e) {
    if (this.painting) {
      let x = e.offsetX;
      let y = e.offsetY;
      let newPoint = { x: x, y: y };
      this.drawLine(this.lastPoint.x, this.lastPoint.y, newPoint.x, newPoint.y);
      this.lastPoint = newPoint;
    }
  }

  // 
  stopDraw() {
    this.painting = false;
  }

  // 
  drawCircle(x, y, radius) {
    this.ctx.save();
    this.ctx.beginPath();
    this.ctx.arc(x, y, radius, 0, Math.PI * 2);
    this.ctx.fill();
    if (this.clear) {
      this.ctx.clip();
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.ctx.restore();
    }
  }

  // 
  drawLine(x1, y1, x2, y2) {
    this.ctx.lineWidth = this.lineWidth; // 
    this.ctx.lineCap = "round";
    this.ctx.lineJoin = "round";
    // console.log(this.clear)
    if (this.clear) {
      this.ctx.save();
      this.ctx.globalCompositeOperation = "destination-out";
      this.ctx.stroke();
      this.ctx.moveTo(x1, y1);
      this.ctx.lineTo(x2, y2);
      this.ctx.closePath();
      this.ctx.clip();
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
      this.ctx.restore();
    } else {
      this.ctx.moveTo(x1, y1);
      this.ctx.lineTo(x2, y2);
      this.ctx.stroke();
      this.ctx.closePath();
    }
  }

  // 
  cleaning() {
    this.clear = true;
    // if (this.clear) {
    //   this.ctx.save();
    //   this.ctx.globalCompositeOperation = "destination-out";
    //   this.ctx.stroke();
    //   this.ctx.closePath();
    //   this.ctx.clip();
    //   this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    //   this.ctx.restore();
    // }
  }

  // 
  reset() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    // console.log(this.canvas.width, this.canvas.height)
    // this.canvas.width = this.canvas.width
    // this.canvas.height = this.canvas.height
  }

  // 
  undo() {
    // console.log(this.history)
    if (this.history.length < 1) return false;
    this.ctx.putImageData(this.history[this.history.length - 1], 0, 0);
    this.history.pop();
  }

  // ,10
  saveData(data) {
    this.history.length === 10 && this.history.shift();
    this.history.push(data);
  }
  // 
  changeLine(value) {
    // console.log(value)
    this.lineWidth = value;
  }
  // 
  checkPen() {
    // console.log()
    this.clear = false;
  }
}
